@import url(https://fonts.googleapis.com/css2?family=Pacifico&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;500&display=swap);
#filter-button {
	background: url('https://wildoceanwhale.org/SightingApps/resources/icons/filter_icon.png');
	background-size: 55%;
	background-repeat: no-repeat;
	background-position: center;
	border: none;
	height: 100%;
	width: auto;
	vertical-align: center;
	position: absolute;
	top: 0px;
	right: 0px;
	padding: 30px;
	padding-right: 40px;
}

#filter-button:hover,
#filter-button.selected {
	background: url('https://wildoceanwhale.org/SightingApps/resources/icons/filter_icon.png'),
		#007006;
	background-size: 55%;
	background-repeat: no-repeat;
	background-position: center;
}

.hidden {
	display: none;
}

#filter-drop-down {
	position: absolute;
	right: 0px;
	top: 82px;
	width: 150px;
	box-shadow: -5px 5px 15px rgb(192, 192, 192);
	padding: 15px 10px;
	text-align: left;
	font-size: 13px;
	font-weight: 500;
	background-color: white;
}

#filter-drop-down li {
	margin-bottom: 5px;
}

#filter-drop-down label {
	margin-left: 5px;
}

#header-bar {
	width: 100%;
	height: 60px;
	text-align: center;
	position: -webkit-sticky;
	position: sticky;
	z-index: 1000;
	top: 0px;
	left: 0px;
	padding-top: 20px;
	background: #17bd32;
	background: linear-gradient(to bottom, #17bd32 0%, #008d18 100%);
	border-bottom: 3pt double white;
}

#header-bar h1 {
	font-size: 45px;
	font-weight: bold;
	font-family: 'Pacifico', cursive;
	color: white;
}

#footer {
	background-color: #008d18;
	height: 70px;
	border-top: 3pt double white;
	color: white;
	text-align: center;
	display: flex;
	flex-direction: column;
	align-items: center;
}

#footer span,
#footer a {
	position: relative;
	top: 7px;
	margin: 7px 0px;
	font-size: 12px;
}

#footer-links {
	display: flex;
	flex-direction: row;
	align-items: center;
}

#created-span {
	color: darkgray;
	display: block;
}

#error-message {
	color: rgb(214, 0, 0);
}

#footer-links a {
	display: block;
}

#footer-links a img {
	width: 20px;
	height: auto;
}

#twitter-link img {
	margin-left: 5px;
}

.loading-spinner {
	display: inline-block;
	position: relative;
	top: 5px;
	width: 30px;
	height: 30px;
}

.loading-spinner div {
	box-sizing: border-box;
	display: block;
	position: absolute;
	width: 24px;
	height: 24px;
	margin: 3px;
	border: 3px solid #fff;
	border-radius: 50%;
	-webkit-animation: loading-spinner 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
	        animation: loading-spinner 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
	border-color: #fff transparent transparent transparent;
}
.loading-spinner div:nth-child(1) {
	-webkit-animation-delay: -0.45s;
	        animation-delay: -0.45s;
}
.loading-spinner div:nth-child(2) {
	-webkit-animation-delay: -0.3s;
	        animation-delay: -0.3s;
}
.loading-spinner div:nth-child(3) {
	-webkit-animation-delay: -0.15s;
	        animation-delay: -0.15s;
}
@-webkit-keyframes loading-spinner {
	0% {
		transform: rotate(0deg);
	}
	100% {
		transform: rotate(360deg);
	}
}
@keyframes loading-spinner {
	0% {
		transform: rotate(0deg);
	}
	100% {
		transform: rotate(360deg);
	}
}

#lanes-box-container {
	flex: 1 1;
	background-color: rgb(255, 255, 189);
	border-top: 1pt solid rgb(255, 196, 0);
	overflow: hidden;
}

#lanes-box-container h2 {
	text-align: center;
	text-transform: uppercase;
	height: 21px;
	font-size: 21px;
	color: rgb(255, 0, 0);
	margin-top: 7px;
	padding-bottom: 5px;
	border-bottom: 1pt dashed rgb(255, 0, 0);
}

.lanes-box-result {
	border-bottom: 1pt dashed rgb(255, 196, 0);
	padding: 7px;
	font-size: 14px;
	overflow: scroll;
}

#lanes-ul {
	overflow: scroll;
	height: calc(100% - 34px);
}

.lane-stops-ul {
	display: none;
	padding: 5px;
	font-size: 12px;
}

.lane-stops-ul.visible {
	display: block;
}

.lane-li {
	font-size: 14px;
	padding: 7px;
	border-bottom: 1pt dashed rgb(255, 196, 0);
}

.show-stops {
	border: none;
	font-size: 14px;
	background-color: transparent;
	color: rgb(0, 0, 187);
	text-decoration: underline;
}

#lines-box-container {
	flex: 1 1;
	background-color: rgb(255, 255, 189);
	border-bottom: 1pt solid rgb(255, 196, 0);
	overflow: hidden;
}

#lines-box-top h2 {
	text-align: center;
	text-transform: uppercase;
	font-size: 21px;
	color: rgb(0, 132, 255);
	margin: 7px 0px;
}

#lines-box-top {
	border-bottom: 1pt dashed rgb(0, 132, 255);
	display: flex;
	flex-direction: column;
	align-items: center;
	padding-bottom: 5px;
	position: sticky;
	position: -webkit-sticky;
	top: 0px;
	background-color: rgb(255, 255, 189);
}

#lines-box-top input {
	background-color: rgba(255, 255, 255, 0.5);
}

.lines-box-result {
	border-bottom: 1pt dashed rgb(255, 196, 0);
	padding: 7px;
	font-size: 14px;
	overflow: scroll;
}

#lines-box-results {
	overflow: scroll;
	height: calc(100% - 65px);
}

.lines-box-result-terminal {
	cursor: pointer;
}

.lines-box-result-details {
	padding: 5px;
	font-size: 12px;
}

li {
	padding: 3px;
}

li b {
	font-weight: bold;
}

#search-bar {
	z-index: 1000;
	width: 40%;
	position: relative;
	top: 5px;
	left: 30%;
	display: flex;
	flex-direction: column;
	align-items: center;
}

#top-section {
	width: 100%;
}

#top-section input {
	width: 97%;
	height: 23px;
	border-radius: 15px;
	border: 1pt solid grey;
	padding-left: 3%;
}

#search-results {
	background-color: white;
	width: 95%;
	font-weight: bold;
}

.search-result-card {
	display: flex;
	flex-direction: row;
	cursor: pointer;
	padding: 5px;
	align-items: center;
}

.search-result-card:hover {
	background-color: darkgray;
}

.search-results-card-left-section img {
	width: 30px;
	height: auto;
	margin-right: 10px;
}

.search-results-card-right-section {
	display: flex;
	flex-direction: column;
}

#no-results-message {
	display: block;
	padding: 10px;
	font-weight: lighter;
}

@media only screen and (max-width: 650px) {
	#search-bar {
		width: 60%;
		left: 20%;
	}

	#top-section input {
		width: 97%;
		height: 25px;
		font-size: 16px;
	}
}

#mapid {
	height: 100%;
	width: 100%;
	position: relative;
}

#mapid b {
	font-weight: bold;
}

#mapid button {
	border: none;
	background-color: transparent;
	color: rgb(0, 68, 255);
	text-decoration: underline;
	display: inline-block;
	padding: 0px;
}

html,
body,
#root {
	width: 100%;
	height: 100%;
}

.App {
	display: flex;
	flex-direction: column;
	width: 100%;
	height: 100%;
	font-family: 'Rubik', sans-serif;
}

#dashboard {
	display: flex;
	flex-direction: row;
	width: 100%;
	height: 80%;
	z-index: 10;
	position: relative;
}

#dashboard #left-section {
	display: flex;
	flex-direction: column;
	height: 100%;
	flex: 1 1;
	justify-content: space-between;
}

#dashboard #right-section {
	display: flex;
	flex-direction: column;
	height: 100%;
	flex: 2 2;
}

@media only screen and (max-width: 650px) {
	#dashboard {
		display: flex;
		flex-direction: column;
		width: 90%;
		height: 100%;
		z-index: 10;
		position: relative;
		padding: 0% 5%;
		background-color: rgb(255, 196, 0);
	}

	#dashboard #left-section {
		display: flex;
		flex-direction: column;
		flex: 1 1;
		justify-content: space-between;
		margin-bottom: 2px;
	}

	#dashboard #right-section {
		display: flex;
		flex-direction: column;
		flex: 0.7 0.7;
	}

	.App {
		height: 150%;
	}
}

